
table {
  border-collapse: collapse;
  font-family: Consolas, "DejaVu Sans Mono", Monaco, monospace;
  font-size: 10pt;
  table-layout: fixed;
  width: 100%;
  margin-top: 1px;
}
.Checkbox {
  width: 30px;
}
.Description {
  width: 10%;
}
.Part {
  width: 10%;
}
.Value {
  width: 15%;
}
.Quantity {
  width: 65px;
}
tbody tr:nth-child(even) {
  background-color: #313b40;
}
tbody tr.highlighted:nth-child(n),
tbody tr:hover {
  background-color: #226022;
}
thead th {
  background-color: #3b4749;
  background-clip: padding-box;
}
th,
td {
  border: 1px solid #777;
  padding: 5px;
  word-wrap: break-word;
  text-align: center;
  position: relative;
}
